@mixin list-item {
  height: $listItemHeight;
  width: $listItemWidth;
  display: flex;
  flex-direction: row;
  border: solid $gap #000;
  box-sizing: border-box;
}

@mixin empty-item {
  height: 0;
  width: 0;
  border: 0;
  content: '';
}
@mixin content {
  font-size: 30px;
  font-family: Arial, Helvetica, sans-serif;
  margin: auto;
}